import React, { Component } from 'react';
import { HashRouter, Route, Link } from "react-router-dom";
import Home from './pages/Home';

class App extends React.Component {
  render() {
    return (
     <HashRouter basename='/'>
      <div>
       <ul>
        <li><Link to="/">Home</Link></li>
       </ul>
       <hr />
       <Route  path="/" element={ <Home/> } />
      </div>
     </HashRouter>
    );
   }
}

export default App;
